import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	data() {
		return {
			token:'',
			banner: [],
			categ:[],
			swiperh: 0,
			imgheights: [],
			dangqian:'',
			address:'',
			master:''
		}
	},
	onLoad() {
		this.loadData();
		this.getCity();
	},
	onShow() {
		//console.log(1);
		//获取用户服务地址
		var _this=this;
		setTimeout(function(){
			if (app.globalData.isRequest == 1) {
				_this.token=app.globalData.user_info.token;
				_this.getDefaultAddress();
			} else {
				// 由于 getUserInfo 是网络请求，可能会在 Page.onLoad 之后才返回
				// 所以此处加入 callback 以防止这种情况
				app.isRequestCallback = isRequest => {
					if (isRequest == 1) {
						//console.log(3);
						_this.token=app.globalData.user_info.token;
						_this.getDefaultAddress();
					}
				}
			}
		},1000);
	},
	methods: {
		//动态获取Banner高度，设置swiper高度
		imgHeight(e, index) {
			var imgwidth = e.detail.width,
				imgheight = e.detail.height,
				//宽高比  
				ratio = imgwidth / imgheight;
			//计算的高度值  
			var viewHeight = 750 / ratio;
			var imgheight = viewHeight;
			var imgheights = this.imgheights;
			//把每一张图片的对应的高度记录到数组里  
			imgheights[index] = imgheight + "rpx";
			this.imgheights = imgheights;
			this.swiperh = imgheights[0];
		},
		bindChange(e) {
			// var imgheights=this.imgheights;
			this.swiperh = this.imgheights[e.detail.current];
		},
		
		loadData() {
			this.request({
				url: interfaces.getIndexData,
				data: {
					type: 1
				},
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.banner=res.data.banner;
						this.categ= res.data.categ;
						this.master=res.data.master;
					}
				}
			})
		},
		goDetail: function(id) {
			wx.navigateTo({
				url: '../master/info?id=' + id,
			})
		},

		//获取定位城市
		getCity: function() {
			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});
			wx.getLocation({
				type: 'gcj02',
				success: res => {
					qqmapsdk.reverseGeocoder({
						location: {
							latitude: res.latitude,
							longitude: res.longitude
						},
						success: addressRes => {
							this.dangqian= addressRes.result.address_component.city;
						}
					})
				},
				fail: err => {
					//console.log(err);
					if (err.errMsg === "getLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							showCancel: false,
							confirmText: '好的',
							content: '未获取到你的地理位置，暂时无法为你提供服务。请检查是否开启定位权限。或尝试重新打开小程序。',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											//console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
		},
		//获取用户的服务地址
		getDefaultAddress(){
			if(this.token){
				this.request({
					url: interfaces.addressDefault,
					header:{
						token:this.token
					},
					method: 'POST',
					success: res => {
						if (res.code == 200) {
							this.address=res.data;
						}
					}
				})
			}	
		},
		//拨打师傅电话
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},
		//跳转分类页面
		goCateg(id){
			if(this.address){
				wx.navigateTo({
					url: '../categ/index?id=' + id,
				})
			}else{
				wx.navigateTo({
					url:'../address/add'
				})
			}
		},
		//跳转更改用户地址
		editAddress(){
			wx.navigateTo({
				url:'../address/edit'
			})
		}
	}
}
